import { mapState } from 'vuex';
export default {
	data() {
		return {
			urls:[
				'./service',
				'./share',
				'./help',
				'./opinion',
				'./message',
				'./about',
				'./config'
			]
		}
	},
	computed:{
		...mapState(["currentUser"])
	},
	onLoad() {
		uni.setNavigationBarTitle({
		　　title:'我的'
		})
	},
	methods: {
		jumpTo(id){
			uni.navigateTo({
				url:this.urls[id]
			})
		}
	}
}
